import { useEffect, useRef, useState } from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Tooltip,
  Legend,
  Filler, // Added for beautiful area gradients
  ScriptableContext
} from "chart.js";
import { Line } from "react-chartjs-2";
import { Box, useToken } from "@chakra-ui/react";
import { useSelector } from "react-redux";
import type { RootState } from "@/store";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Tooltip,
  Legend,
  Filler
);

export default function LineChart() {
  const { metric } = useSelector((state: RootState) => state.filters);
  // Pull teal.500 from your Chakra theme tokens
  const [brandColor] = useToken("colors", ["teal.500"]);

  const data = {
    labels: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
    datasets: [
      {
        fill: true, // Enables the gradient area
        label: metric.toUpperCase(),
        // Note: In a real app, generateMockData should be memoized or inside useEffect
        data: [400, 450, 420, 500, 600, 550, 700, 800, 750, 900, 850, 1000],
        borderColor: brandColor,
        // Creating a beautiful fade-out gradient
        backgroundColor: (context: ScriptableContext<"line">) => {
          const ctx = context.chart.ctx;
          const gradient = ctx.createLinearGradient(0, 0, 0, 300);
          gradient.addColorStop(0, "rgba(49, 151, 149, 0.4)");
          gradient.addColorStop(1, "rgba(49, 151, 149, 0)");
          return gradient;
        },
        tension: 0.4,
        pointRadius: 0, // Hides points until hover for a cleaner look
        pointHoverRadius: 6,
        borderWidth: 3,
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: { display: false }, // Cleaner UI: Use Chakra text for titles instead
      tooltip: {
        backgroundColor: "#1A202C", // Matches gray.800
        padding: 12,
        cornerRadius: 8,
        titleFont: { size: 14, weight: 'bold' as const },
        bodyFont: { size: 14 },
      },
    },
    scales: {
      x: {
        grid: { display: false }, // Remove vertical lines for modern "airy" look
        ticks: { color: "#718096" } // gray.500
      },
      y: {
        border: { dash: [4, 4] }, // Dashed horizontal lines
        grid: { color: "rgba(0,0,0,0.05)" },
        ticks: { color: "#718096" }
      },
    },
  };

  return (
    <Box 
      h="350px" 
      w="full" 
      p={4} 
      bg="white" 
      _dark={{ bg: "gray.900" }} 
      borderRadius="xl"
      boxShadow="sm"
    >
      <Line data={data} options={options} />
    </Box>
  );
}