import { 
  Box, 
  Flex, 
  Heading, 
  Input, 
  IconButton, 
  HStack, 
  Text,
  InputGroup, // If you have the snippet, otherwise use Box + Position
} from "@chakra-ui/react";
import { 
  LuSearch, 
  LuBell, 
  LuMoon, 
  LuSun, 
  LuCircleUser
} from "react-icons/lu";
import { useState } from "react";

export default function Topbar() {
  const [isDark, setIsDark] = useState(false);

  return (
    <Flex
      h="70px"
      align="center"
      justify="space-between"
      px={6}
      bg="white/80"
      _dark={{ bg: "gray.900/80" }}
      backdropFilter="blur(10px)"
      position="sticky"
      top="0"
      zIndex="10"
    >
      {/* Left Side: Page Title or Mobile Menu Trigger */}
      <HStack gap={4}>
        <Heading size="md" fontWeight="semibold" color="gray.700" _dark={{ color: "white" }}>
          Overview
        </Heading>
      </HStack>

      {/* Center: Search Bar (Hidden on small mobile) */}
      <Box display={{ base: "none", md: "block" }} maxW="400px" w="full" mx={8}>
        <Box position="relative">
          <Box
            position="absolute"
            left="3"
            top="50%"
            transform="translateY(-50%)"
            zIndex="1"
            color="gray.400"
          >
            <LuSearch size={18} />
          </Box>
          <Input
            placeholder="Search everything..."
            pl="10"
            bg="gray.100"
            border="none"
            _focus={{ bg: "white", ring: "2px", ringColor: "teal.500" }}
            _dark={{ bg: "gray.800", _focus: { bg: "gray.700" } }}
            borderRadius="full"
          />
        </Box>
      </Box>

      {/* Right Side: Actions & Profile */}
      <HStack gap={3}>
        {/* Notifications */}
        <IconButton
          variant="ghost"
          aria-label="Notifications"
          borderRadius="full"
          color="gray.600"
          _dark={{ color: "gray.400" }}
        >
          <LuBell size={20} />
        </IconButton>

        {/* Theme Toggle */}
        <IconButton
          variant="ghost"
          aria-label="Toggle Theme"
          borderRadius="full"
          onClick={() => setIsDark(!isDark)}
        >
          {isDark ? <LuSun size={20} /> : <LuMoon size={20} />}
        </IconButton>

        {/* User Profile */}
        <HStack 
          gap={3} 
          pl={2} 
          cursor="pointer" 
          _hover={{ opacity: 0.8 }}
        >
          <Box textAlign="right" display={{ base: "none", sm: "block" }}>
            <Text fontSize="sm" fontWeight="bold" lineHeight="1">
              Alex Rivera
            </Text>
            <Text fontSize="xs" color="gray.500">
              Admin
            </Text>
          </Box>
          <Box
            w="40px"
            h="40px"
            bg="teal.500"
            borderRadius="full"
            display="flex"
            alignItems="center"
            justifyContent="center"
            color="white"
          >
            <LuCircleUser size={24} />
          </Box>
        </HStack>
      </HStack>
    </Flex>
  );
}