"use client";

import { useRouter } from "next/navigation";
import { useSession } from "next-auth/react";
import { useEffect } from "react";
import { Box, Heading, Text, Spinner, Center } from "@chakra-ui/react";
import DashboardLayout from "../components/layout/DashboardLayout";
import { useQuery } from "@tanstack/react-query";
import { fetchChartOverview } from "../lib/api/charts";
import { useSelector } from "react-redux";
import type { RootState } from "../store";
import LineChart from "../components/Charts/LineChart";

export default function DashboardPage() {
  const { data: session, status } = useSession();
  const router = useRouter();

  useEffect(() => {
    if (status === "unauthenticated") {
      router.push("/login");
    }
  }, [status, router]);

  const { metric, region, dateRange } = useSelector(
    (state: RootState) => state.filters
  );

  const { data, isLoading, error } = useQuery({
    queryKey: ["chart-overview", metric, region, dateRange],
    queryFn: () => fetchChartOverview(metric, "monthly", region),
    staleTime: 1000 * 60,
  });

  if (status === "loading") {
    return (
      <Center h="100vh">
        <Spinner size="xl" color="teal.500" />
        <Text ml={4}>Verifying Session...</Text>
      </Center>
    );
  }

  if (status === "unauthenticated") return null;

  return (
    <DashboardLayout>
      <Box p={4}>
        <Heading size="lg" mb={2}>
          Welcome, {session?.user?.name || session?.user?.email}
        </Heading>
        <Text color="gray.600" _dark={{ color: "gray.400" }} mb={6}>
          Your analytics overview is ready.
        </Text>

        {isLoading ? (
          <Spinner color="teal.500" />
        ) : error ? (
          <Text color="red.500" fontWeight="bold">Error connecting to data service.</Text>
        ) : (
          <Box bg="white" _dark={{ bg: "gray.800" }} p={6} borderRadius="2xl" boxShadow="sm">
             <LineChart
               labels={data.labels}
               data={data.data}
               title={data.metric.toUpperCase()}
             />
          </Box>
        )}
      </Box>
    </DashboardLayout>
  );
}